@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@900&display=swap);
body {
  margin: 0;
  width: 100vw;
  height: 100vh;
  background-color: #545454;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.controller{
  flex-direction: column;
}

.buttons{
  display: flex;
  font-size: 2rem;
}

.btn{
  margin: 4px;
}

.btn:active{
  font-size: 2.2rem;
  color: rgba(255, 255, 255, 0.425);
}
.timer{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 10vh 30vh 10vh;
  grid-template-areas: 
    "label label"
    "time time"
    "play reset";
  text-align: center;
  
}

#timer-label{
  grid-area: label;
  font-size: 2rem;
  color: #5f5e5c;
  background-color: #e1e0de;
  padding: 8px;
  border-radius: 32px 32px 0 0;
  text-shadow: none;
}

#time-left{
  grid-area: time;
  font-size: 8rem;
  padding-bottom: 18px;
  border-radius: 0 0 32px 32px;
  background-color: #7fbbc5;
  text-shadow:-6px 2px 0 #c78e70;
  color: #e1e0de;
}

#start_stop{
  grid-area: play;
  font-size: 2rem;
}

#reset{
  grid-area: reset;
  font-size: 2rem;
}

#start_stop:active{
  color: rgba(255, 255, 255, 0.425);
}

#reset:active{
  color: rgba(255, 255, 255, 0.425);
}

@media (min-width: 416px){
  #time-left{
    font-size: clamp(54px, 10vw, 144px);
  }
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: #e1e0de;
  font-family: 'Noto Serif JP', serif;
  text-shadow: -2px 0px 0 #373737;
}

.controllers{
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 416px){
  .App{
    width: 33%;
    height: 100%;
  }
}
